$(window).on('load', function () {
    function heightStoris() {
        
      let height = $(window).height()
      width = height / 1.77777 - 15
      height = height * 0.97
      right = width / 2
      $('.white-popup .wrapper-storis-popup').css('height', height);
      $('.white-popup .wrapper-storis-popup').css('width', width);
      $('.white-popup .right-storis').css(`left`, `calc(50% + ${right}px + 10px)`);
      $('.white-popup .left-storis').css(`left`, `calc(50% - ${right}px - 40px)`);
    }

    function heightStoris2() {
        
      let height = $(window).height()
      width = height / 1.77777
      height = height
      right = width / 2
      $('.white-popup .wrapper-storis-popup').css('height', height);
      $('.white-popup .wrapper-storis-popup').css('width', width);
      $('.white-popup .right-storis').css(`left`, `calc(50% + ${right}px + 10px)`);
      $('.white-popup .left-storis').css(`left`, `calc(50% - ${right}px - 40px)`);
    }
    if($(window).width() > 735){
      heightStoris();
    } else {
      heightStoris2();
    }    

  });
  